export default function(props,status){
    if(status === 'add' || status==='copy' || status === 'create'){
        let SaveAdd=false,DataTemplet=false;
        if(status==='add'){
            SaveAdd = true;
            DataTemplet = true;
        }else if(status==='copy'){
            SaveAdd = true;
            DataTemplet = true;
        }else if(status === 'create'){
            SaveAdd = false;
            DataTemplet = false;
        }
        props.button.setButtonsVisible({
            Add : false,//新增
            Edit : false,//修改
            Save : true,//保存
            SaveAdd:SaveAdd,//保存新增
            Delete : false,//删除
            Cancel : true,//取消
            Refresh : false,//刷新
            Copy : false,//复制
            BatchUpdate : false,//批改
            BatchUpdateWizard : false,//向导批改
            BatchUpdateRule : false,//按规则批改
            Assign : false,//分配
            CancelAssign : false,//取消分配
            AssignWizard : false,//向导分配
            AssignStatus : false,//已分配组织查询
            assistantMenu : false,//辅助功能下拉按钮组
            CreateVersion : false,//创建新版本
            File : false,//附件管理
            OrgBrowse : false,//按组织查看
            QueryOrgDoc : false,//查看组织档案
            Associate : false, //申请单查询
            Enable:false,//启用
            Disable:false, //停用
            Print:false,//打印
            Upgrade : false,//升级
            convert_add:true,
            More:false,
            DataTemplet:DataTemplet
        });
        props.cardPagination.setCardPaginationVisible('cardPaginationBtn', false);
    }else if(status === 'edit'){
        props.button.setButtonsVisible({
            Add : false,//新增
            Edit : false,//修改
            Save : true,//保存
            SaveAdd:false,//保存新增
            Delete : false,//删除
            Cancel : true,//取消
            Refresh : false,//刷新
            Copy : false,//复制
            BatchUpdate : false,//批改
            BatchUpdateWizard : false,//向导批改
            BatchUpdateRule : false,//按规则批改
            Assign : false,//分配
            CancelAssign : false,//取消分配
            AssignWizard : false,//向导分配
            AssignStatus : false,//已分配组织查询
            assistantMenu : false,//辅助功能下拉按钮组
            CreateVersion : false,//创建新版本
            File : false,//附件管理
            OrgBrowse : false,//按组织查看
            QueryOrgDoc : false,//查看组织档案
            Associate : false, //申请单查询
            Enable:false,//启用
            Disable:false, //停用
            Print:false,//打印
            Upgrade:false,//升级
            convert_add:true,
            More:false,
            DataTemplet : false
        });
        props.cardPagination.setCardPaginationVisible('cardPaginationBtn', false);
    }else{
        props.button.setButtonsVisible({
            Add : true,//新增
            Edit : true,//修改
            Save : false,//保存
            SaveAdd:false,//保存新增
            Delete : true,//删除
            Cancel : false,//取消
            Refresh : true,//刷新
            Copy : true,//复制
            BatchUpdate : true,//批改
            BatchUpdateWizard : true,//向导批改
            BatchUpdateRule : true,//按规则批改
            Assign : true,//分配
            CancelAssign : true,//取消分配
            AssignWizard : true,//向导分配
            AssignStatus : true,//已分配组织查询
            assistantMenu : true,//辅助功能下拉按钮组
            CreateVersion : true,//创建新版本
            File : true,//附件管理
            OrgBrowse : true,//按组织查看
            QueryOrgDoc : true,//查看组织档案
            Associate : true, //申请单查询
            Enable:true,//启用
            Disable:true, //停用
            Print:true,//打印
            Upgrade:true,//升级
            convert_add:false,
            More:true,
            DataTemplet:false
        });
        props.cardPagination.setCardPaginationVisible('cardPaginationBtn', true);
        let pk_material = props.form.getFormItemsValue('material','pk_material');
        if(!pk_material || !pk_material.value){
            props.button.setDisabled({
                Edit : true,
                Delete:true,
                Copy:true,
                Enable:true,
                Disable:true,
                CreateVersion:true,
                Associate:true,
                Assign : true,
                AssignStatus:true,
                File:true,
                Print:true,
                Output:true,
                Refresh:true,
                Upgrade:true,
                DataTemplet:false,
                BatchUpdate : true
            });
        }else{
            props.button.setDisabled({
                Edit : false,
                Delete:false,
                Copy:false,
                CreateVersion:false,
                Assign : false,
                AssignStatus:false,
                File:false,
                Print:false,
                Output:false,
                Refresh:false,
                Upgrade:false,
                BatchUpdate : false
            });
            let enablestate = props.form.getFormItemsValue('material','enablestate');
            if(enablestate){
                if(enablestate.value === '2'){
                    props.button.setDisabled({
                        Enable:true,
                        Disable:false
                    });
                }else{
                    props.button.setDisabled({
                        Enable:false,
                        Disable:true
                    });
                }
            }else{
                props.button.setDisabled({
                    Enable:true,
                    Disable:true
                });
            }
            let pk_material_pf = props.form.getFormItemsValue('material','pk_material_pf');
            if(!pk_material_pf || !pk_material_pf.value){
                props.button.setDisabled({
                    Associate:true
                });
            }else {
                props.button.setDisabled({
                    Associate:false
                });
            }
        }
    }
}